  <div></div>
</template>

<script>
import 'leaflet-draw';
import 'leaflet/dist/leaflet.css';
import './styles.scss';

// import { LMap, LTileLayer } from 'vue2-leaflet';

export default {
  // components: {
  //   LMap,
  //   LTileLayer
  // },
  props: {
    position: {
      type: String,
      default: 'topleft'
    },
    refs: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      map: null,
      editableLayers: null
    };
  },
  watch: {
    map() {
      this.init();
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.map = this.refs || this.$refs.map.mapObject;
    });
  },
  methods: {
    init() {
      if (!this.map) return;

      this.editableLayers = new L.FeatureGroup();
      this.map.addLayer(this.editableLayers);

      const options = {
        position: this.position,
        draw: {
          circle: false,
          marker: false,
          polyline: false,
          rectangle: false,
          circlemarker: false,
          polygon: {
            metric: true,
            showArea: true,
            showLength: true,
            zIndexOffset: 0,
            allowIntersection: false, // Restricts shapes to simple polygons
            drawError: {
              color: '#e1e100', // Color the shape will turn when intersects
              message: '<strong>Oh snap!<strong> you can not draw that!' // Message that will show when intersect
            },
            shapeOptions: {
              color: '#2393f5'
            }
          }
        },
        edit: {
          featureGroup: this.editableLayers, //REQUIRED!!
          remove: true
        }
      };

      const drawControl = new L.Control.Draw(options);
      this.updateLabels();
      this.map.addControl(drawControl);

      this.$nextTick(() => {
        this.createEvents();
      });
    },
    createEvents() {
      this.map.on('draw:created', (e) => {
        const layer = e.layer;
        this.editableLayers.addLayer(layer);
        this.$emit('created', layer);
      });
      this.map.on('draw:edited', (e) => {
        this.$emit('edited', e.layers);
      });
      this.map.on('draw:deleted', (e) => {
        this.$emit('deleted', e.layers);
      });
    },
    updateLabels() {
      L.drawLocal.draw.toolbar.actions.title = '';
      L.drawLocal.draw.toolbar.actions.text = '';
      L.drawLocal.draw.toolbar.finish.title = '';
      L.drawLocal.draw.toolbar.finish.text = '';
      L.drawLocal.draw.toolbar.undo.title = '  ';
      L.drawLocal.draw.toolbar.undo.text = '  ';
      L.drawLocal.draw.toolbar.buttons.polygon =
        '<span class="mdi mdi-vector-polyline"></span>';

      L.drawLocal.draw.handlers.polygon.tooltip.start =
        '     ';
      L.drawLocal.draw.handlers.polygon.tooltip.cont =
        '    ';
      L.drawLocal.draw.handlers.polygon.tooltip.end =
        '        ';

      L.drawLocal.edit.toolbar.actions.save.title = '';
      L.drawLocal.edit.toolbar.actions.save.text = '';
      L.drawLocal.edit.toolbar.actions.cancel.title = '';
      L.drawLocal.edit.toolbar.actions.cancel.text = '';

      L.drawLocal.edit.toolbar.actions.clearAll.title = ' ';
      L.drawLocal.edit.toolbar.actions.clearAll.text = ' ';

      L.drawLocal.edit.toolbar.buttons.edit = '';
      L.drawLocal.edit.toolbar.buttons.editDisabled = '   ';
      L.drawLocal.edit.toolbar.buttons.remove = '';
      L.drawLocal.edit.toolbar.buttons.removeDisabled =
        '    ';

      L.drawLocal.edit.handlers.edit.tooltip.text =
        '      ';
      L.drawLocal.edit.handlers.edit.tooltip.subtext = '  ';
      L.drawLocal.edit.handlers.remove.tooltip.text =
        '         ';
    }
  }
};
</script>

